//get particular page
export const getPageDistrict =
  ({ number, postsPerPage }) =>
  async (dispatch) => {
    try {
      dispatch(actions.loadingAction());
      const { data } = await API.getPageDistrict(number, postsPerPage);
      dispatch(actions.getDistrictSuccessAction(data));
    } catch (error) {
      dispatch(actions.getDistrictFailAction(error));
      errorFunction(error);
    }
  };
export const createDistrict =
  ({ name, name_nepali, is_active }, history) =>
  async (dispatch) => {
    try {
      const body = JSON.stringify({ name, name_nepali, is_active });
      dispatch(actions.loadingAction());
      const { data } = await API.createDistrict(body);
      dispatch(actions.createDistrictSuccessAction(data));
      successFunction("District Added Successfully.");
      history.push("/district");
    } catch (error) {
      dispatch(actions.createDistrictFailAction(error));
      errorFunction("Failed to create District.");
    }
  };
// Delete  bank
export const deleteDistrict = (id) => async (dispatch) => {
  try {
    dispatch(actions.loadingAction());
    const { data } = await API.deleteDistrict(id);
    dispatch(actions.deleteDistrictSuccessAction(data));
    successFunction("District Deleted Successfully.");
  } catch (error) {
    dispatch(actions.deleteDistrictFailAction(error));
    errorFunction(error);
  }
};

//update
export const updateDistrict = (id, history, values) => async (dispatch) => {
  try {
    dispatch(actions.loadingUpdatedAction());
    const { name, name_nepali, remarks, is_active } = values;
    const body = JSON.stringify({ name, name_nepali, remarks, is_active });
    const { data } = await API.updateDistrict(id, body);
    dispatch(actions.updatedDistrictSuccessAction(data));
    successFunction("District Updated Successfully ");
    history.push("/district");
  } catch (error) {
    dispatch(actions.updatedDistrictFailAction(error));
    errorFunction("Failed to Update District");
  }
};
//handle Search
export const handleSearch = (search, postsPerPage) => async (dispatch) => {
  try {
    dispatch(actions.loadingAction());
    const { data } = await API.handleSearch(search, postsPerPage);
    dispatch(actions.getDistrictSuccessAction(data));
  } catch (error) {
    dispatch(actions.getDistrictFailAction(error));
    errorFunction(error);
  }
};
