import { districtConstants } from "./constants";
// actions
export const loadingAction = () => ({
  type: districtConstants.LOADING_DISTRICT,
});
export const loadingUpdatedAction = () => ({
  type: districtConstants.LOADING_UPDATED,
});
export const getDistrictSuccessAction = (data) => ({
  type: districtConstants.GET_DISTRICT_SUCCESS,
  payload: data,
});
export const getDistrictFailAction = (error) => ({
  type: districtConstants.GET_DISTRICT_FAIL,
  payload: error,
});

export const createDistrictSuccessAction = (data) => ({
  type: districtConstants.CREATE_DISTRICT_SUCCESS,
  payload: data,
});

export const createDistrictFailAction = (error) => ({
  type: districtConstants.CREATE_DISTRICT_FAIL,
  payload: error,
});

export const deleteDistrictSuccessAction = (data) => ({
  type: districtConstants.DISTRICT_DELETED_SUCCESS,
  payload: data,
});

export const deleteDistrictFailAction = (error) => ({
  type: districtConstants.DISTRICT_DELETED_FAIL,
  payload: error,
});

export const updatedDistrictSuccessAction = (data) => ({
  type: districtConstants.DISTRICT_UPDATE_SUCCESS,
  payload: data,
});

export const updatedDistrictFailAction = (error) => ({
  type: districtConstants.DISTRICT_UPDATE_FAIL,
  payload: error,
});
