import React, { useState } from "react";
import { Formik, Form, Field, ErrorMessage } from "formik";
import * as Yup from "yup";
import TextError from "../../Component/TextError/TextError";
import { useSelector, useDispatch } from "react-redux";
import { errorFunction } from "../../Component/Alert";
import { createDistrict, updateDistrict } from "../../Redux/District/thunk";
import Button from "../../Component/Button/Button";
import { checkRedundantDataDistrictName } from "../../utils/redundant/district";
import { useHistory } from "react-router-dom";
import preeti from "preeti";
import { englishAlphabets } from "../../utils/englishAlphabets";
const CreateDistrict = ({ currentPage }) => {
  // props
  const history = useHistory();
  const district = useSelector((state) => state.district.district);
  const loading = useSelector((state) => state.district.loading);
  const edit = useSelector((state) => state.district.edit);
  const dispatch = useDispatch();
  //state for disabling the submit button
  const [lock, setLock] = useState(false);
  //initial state of the form
  const initialState = {
    name: edit ? district?.name : "",
    name_nepali: edit ? district?.name_nepali : "",
    is_active: edit ? (district?.is_active ? true : true) : true,
    remarks: "",
  };

  //validation rules of the form
  const validationSchema = Yup.object().shape({
    name: Yup.string()
      .required("Name is required.")
      .min(1, "Name must be at least 1 characters.")
      .max(50, "Name must be at least 50 characters."),
    name_nepali: Yup.string()
      .required("Name is required.")
      .min(1, "Name must be at least 1 characters.")
      .max(50, "Name must be at least 50 characters."),
    remarks:
      edit &&
      Yup.string()
        .required("Remarks is required.")
        .min(1, "Remarks Name must be at least 1 characters."),
    is_active: Yup.bool(),
  });
  const onSubmit = (values) => {
    setLock(true);
    if (edit) {
      const id = district?.id;
      dispatch(updateDistrict(id, history, values));
    } else {
      dispatch(createDistrict(values, history, currentPage));
    }
    setLock(false);
  };

  // function which checks whether the District is added already or not
  const handleChangeName = async (data) => {
    if (edit && data !== district?.name) {
      if (data.trim() && data !== "") {
        const result = await checkRedundantDataDistrictName(data);
        if (result) {
          errorFunction("District with this name is already added ") ||
            setLock(true);
        } else {
          setLock(false);
        }
      }
    }
    if (!edit && data.trim() && data !== "") {
      const result = await checkRedundantDataDistrictName(data);
      if (result) {
        errorFunction("District name with this  is already added. ") ||
          setLock(true);
      } else {
        setLock(false);
      }
    }
  };
  let backSpace = false;
  const handleChange = (e, formik) => {
    const letter = e.target.value.slice(-1);
    if (e.target.name === "name_nepali") {
      if (e.target.value.length < formik.values.name.length) {
        let word = formik.values.name.slice(0, formik.values.name.length - 1);
        formik.setFieldValue("name", word);
        formik.setFieldValue("name_nepali", preeti(word));
      } else if (e.target.value.length === 0) {
        formik.setFieldValue("name", "");
        formik.setFieldValue("name_nepali", "");
      }
      //  else if (formik.values.name.length === e.target.value.length) {
      //   formik.setFieldValue("name", e.target.value);
      //   formik.setFieldValue("name_nepali", preeti(e.target.value));
      // }
      else {
        if (backSpace) {
          formik.setFieldValue("name", e.target.value);
          formik.setFieldValue("name_nepali", preeti(e.target.value));
        } else {
          formik.setFieldValue("name", formik.values.name + letter);
          formik.setFieldValue(
            "name_nepali",
            preeti(formik.values.name + letter)
          );
        }
      }
    }
  };
  const handleCopy = (e) => {
    e.preventDefault();
    e.nativeEvent.stopImmediatePropagation();
    errorFunction("You cannot copy the text.");
  };
  const handlePaste = (e) => {
    e.preventDefault();
    e.nativeEvent.stopImmediatePropagation();
    errorFunction("You cannot Paste the text.");
  };
  const handleCut = (e) => {
    e.preventDefault();
    e.nativeEvent.stopImmediatePropagation();
    errorFunction("You cannot cut the text.");
  };
  const handleKeyDown = (e) => {
    if (e.key === "Backspace") {
      backSpace = true;
    } else {
      backSpace = false;
    }
  };
  return (
    <>
      <div className="row  mt-2">
        <div className="col-12 ">
          <div className="registration-process ">
            <Formik
              enableReinitialize={true}
              initialValues={initialState}
              validationSchema={validationSchema}
              onSubmit={onSubmit}
            >
              {(formik) => {
                return (
                  <Form autoComplete="off">
                    <div className="form-group row my-2 justify-content-center">
                      <div className="col-6">
                        <label
                          htmlFor="name_nepali"
                          className="fw-bold float-start"
                        >
                          Name
                          <strong className="text-danger ">*</strong>
                        </label>
                        <Field
                          type="text"
                          name="name_nepali"
                          className="form-control "
                          placeholder="Name"
                          onCopy={handleCopy}
                          onCut={handleCut}
                          onPaste={handlePaste}
                          onKeyDown={handleKeyDown}
                          onChange={(e) => {
                            const data =
                              formik.values.name + e.target.value.slice(-1);
                            handleChange(e, formik);
                            handleChangeName(data);
                          }}
                          autoFocus
                        />

                        <ErrorMessage
                          name="name_nepali"
                          component={TextError}
                        />
                      </div>
                    </div>

                    <div className="form-group row my-2 justify-content-center">
                      {edit && (
                        <div className="col-3">
                          <label htmlFor="remarks" className="fw-bold">
                            Remarks
                            <span className="text-danger">*</span>
                          </label>
                          <textarea
                            type="text"
                            name="remarks"
                            onChange={(e) => {
                              formik.setFieldValue(
                                "remarks",
                                e.target.value.trim()
                              );
                            }}
                            className="form-control"
                            placeholder="Enter remarks"
                          />
                          <ErrorMessage name="remarks" component={TextError} />
                        </div>
                      )}
                    </div>
                    <div className="form-group row my-2 justify-content-center">
                      <div className="col-12 text-center">
                        <Field
                          type="checkbox"
                          name="is_active"
                          id="is_active"
                          className="filled-in chk-col-blue"
                        />
                        <label htmlFor="is_active" className="fw-bold mx-2">
                          Active
                        </label>
                      </div>
                    </div>
                    <div className="form-group text-center">
                      {!edit ? (
                        <Button
                          type={"btn"}
                          className={"btn btn-primary"}
                          loading={loading}
                          disabled={lock || !formik.dirty}
                          title={"Create"}
                          content={"Add"}
                        />
                      ) : (
                        <Button
                          type={"btn"}
                          className={"btn btn-primary "}
                          loading={loading}
                          disabled={lock}
                          title={"Update"}
                          content={"Update"}
                        />
                      )}
                    </div>
                  </Form>
                );
              }}
            </Formik>
          </div>
        </div>
      </div>
    </>
  );
};
export default CreateDistrict;
