import axiosInstance from "../../utils/axios";

//obtaining the paginated data
export const getDistrict = (postsPerPage) =>
  axiosInstance.get(
    `api/v1/core-app/district?offset=0&limit=${postsPerPage}&ordering=-id`
  );

//obtaining all data
export const getAllDistrict = () =>
  axiosInstance.get(`api/v1/core-app/district?ordering=-id`);

//obtaining the previous page data from paginated data
export const getPrevious = (previous) => axiosInstance.get(previous);

//obtaining the next page data from paginated data
export const getNext = (next) => axiosInstance.get(next);

//obtaining the particular page data from paginated data
export const getPageDistrict = (number, postsPerPage) =>
  axiosInstance.get(
    `api/v1/core-app/district?offset=${
      (number - 1) * postsPerPage
    }&limit=${postsPerPage}&ordering=-id`
  );

//creating function
export const createDistrict = (body) =>
  axiosInstance.post(`api/v1/core-app/district`, body);

//deleting function
export const deleteDistrict = (id) =>
  axiosInstance.delete(`api/v1/core-app/district/${id}`);

//update function
export const updateDistrict = (id, body) =>
  axiosInstance.patch(`api/v1/core-app/district/${id}`, body);

//searching function
export const handleSearch = (search, postsPerPage) =>
  axiosInstance.get(
    `api/v1/core-app/district?offset=0&limit=${postsPerPage}&search=${search}`
  );
export const checkRedundantDataName = (data, cancelToken) =>
  axiosInstance.get(`api/v1/core-app/district?name=${data}`, {
    cancelToken: cancelToken.token,
  });
